.About {
  text-align: left;
  font-size: 16px;
  margin-top: 12vh;
  line-height: 1.6em;
  margin-bottom: 60px;
}

.About p a {
  text-decoration: underline;
  text-decoration-thickness: 1px;
}

.highlight {
  position: relative;
  z-index: 1;
  white-space: pre;
}

.highlight::before {
  content: '';
  z-index: -1;
  top: 6px;
  left: 1px;
  right: -1px;
  bottom: -1px;
  background-color: var(--accent);
  box-shadow: 2px 2px rgba(0, 0, 0, 0.3);
  position: absolute;
}

.heart {
  vertical-align: text-top;
}

@media screen and (min-width: 760px) {
  .About {
    font-size: 24px;
  }

  .highlight::before {
    left: 3px;
    right: -2px;
  }
}
@media screen and (max-width: 300px) {
  .highlight {
    display: none;
  }
}